.ticker {
	font-weight:bold;	
	font-size:2rem;
	color:#FFFFFF;
	background-color:#d21243;
	padding:0.5rem;
	display:block;
}

ul.marquee {
	/* required styles */
	display: block;
	padding: 1rem;
	margin: 0;
	list-style: none;
	position: relative;
	overflow: hidden;

	/* optional styles for appearance */

	background-color:#d21243;
}

ul.marquee li {
	/* required styles */
	position: absolute;
	top: -999em;
	left: 0;
	display: block;
	white-space: nowrap; /* keep all text on a single line */
	height:2.6rem;

	/* optional styles for appearance */
	color:#FFFFFF;
	font-weight:600;
	font-size:1.3rem
}

